"use client";
import { useSession, signOut } from "next-auth/react";
import Link from "next/link";

export default function Dashboard() {
  const { data: session, status } = useSession();

  if (status === "loading") {
    return <p>Loading...</p>;  // jab tak session check ho raha hai
  }

  if (status === "unauthenticated") {
    return <Link href="/login">Please login first</Link>;
  }

  const id = session.user.id
  return (

    <>
    <div className="mx-30 my-10">
      <h1>Welcome {session.user.name}</h1>
      <p>Email: {session.user.email}</p>
      <p>User id is {id}</p>
      <button onClick={() => signOut()}>Logout</button>
    </div>

    <div className="rounded-md cursor-pointer mx-30 flex bg-gray-200 mt-10 w-fit p-2">

      <div>
        <Link href={`/api/users/${id}/update`}>Update your details</Link>
        
 
    </div>



    </div>
    
    </>
  );
}
